.bracket {
  /* max-width: 650px;
  max-height: 388px; */
}

.bracket-container-transition {
  transition: transform ease 1s;
}

.round {
  max-width: 360px;
}

.round-names {
  max-width: 360px;
}

.section {
  min-width: 240px;
}

.rd16 .section {
  height: 290px;
}
.rd16 .section .merger {
  height: 156px;
}

.quarterfinals .section {
  height: 608px;
}
.quarterfinals .section .matchups,
.quarterfinals .section .connector {
  margin: 77px 0;
}
.quarterfinals .section .merger {
  height: 320px;
}

.semifinals .section {
  height: 1244px;
}
.semifinals .section .matchups,
.semifinals .section .connector {
  margin: 236px 0;
}
.semifinals .section .merger {
  height: 638px;
}

.final .section {
  height: 1244px;
}
.final .section .matchups {
  margin: 554px 0;
}
.final .section .connector {
  display: none;
}
